@import "variables";
@import "mixins";
// global
* {
	margin: 0;
	padding: 0;
}
body {
	background: #fff;
	font-weight: normal;
	position: relative;
  	font: 14px/1.5 "Helvetica Neue",Helvetica,Arial,"Microsoft Yahei","Hiragino Sans GB","Heiti SC","WenQuanYi Micro Hei",sans-serif;
}
#wrap {
	position: relative;
	width: 100%;
	overflow: hidden;
}
// heading
h1, h2, h3, h4, h5, h6 {
	font-weight: normal;
	color: $gray-base;
	font-family: "Helvetica Neue",Helvetica,Arial,"Microsoft Yahei","Hiragino Sans GB","Heiti SC","WenQuanYi Micro Hei",sans-serif;
	font-weight: bold;
	letter-spacing: 0px;
}
h1 {
	font-size: 42px;
}
h2 {
	font-size: 36px;
}
h3 {
	font-size: 30px;
}
h4 {
	font-size: 24px;
}
h5 {
	font-size: 18px;
}
h6 {
	font-size: 16px;
	font-weight: bold;
}
p {
	font-weight: 400;
	color: $gray-l-1;
	font-family: "Microsoft Yahei","Hiragino Sans GB","Heiti SC","WenQuanYi Micro Hei",sans-serif;
	line-height: 22px;
	font-size: 14px;
	text-rendering: optimizeLegibility;
	.intro-small {
		width: 80%;
		margin: 0 auto;
	}
}
a {
	@include all-transition(.4s, ease-in-out);
	text-decoration: none !important;
}
img {
	@include transition(.4s, ease-in-out);
	display: inline-block !important;
}
ul {
	margin-bottom: 0px;
}
li {
	list-style: none;
}
a {
	text-decoration: none;
	color: #141414;
	&:hover,
	&:focus {
		color: #00b1f1;
	}
}
.position-center-center {
	left: 50%;
	position: absolute;
	top: 50%;
	@include transform(-50%, -50%);
}

// buttons
.btn {
	border: none;
	color: #fff;
	display: inline-block;
	padding: 2px 21px;
	font-weight: normal;
	font-size: 14px;
	border-radius: 0;
	font-family: "Microsoft Yahei","Hiragino Sans GB","Heiti SC","WenQuanYi Micro Hei",sans-serif;
	line-height: 40px;
	background: rgba(0,0,0,.6);
	position: relative;
	z-index: 1;
	&:hover {
		background: #000;
		color: #fff;
	}
}
.btn-white {
	background: #fff;
	color: #ab4e52 !important;
	&:hover {
		background: #ab4e52;
		color: #fff !important;
	}
}
// header
header {
	width: 100%;
	z-index: 99;
	position: absolute;
	background: #fff;
	padding: 40px 0;
	@include linear-gradient-dark(rgba(255,255,255,.5), rgba(255,255,255,0));
}

.navbar{
  padding: 30px 0 30px 0;
  .brand img{
    padding-top: 11px;
  }
  li{
    a{
      color: #fff;
      font-size: 16px;
      font-weight: 500;
      padding-right: 20px;
    }
    a:hover, a:focus{
      background-color: transparent;
     opacity: 0.8;
      text-decoration: none;
      outline: medium none;
    }
  }
  .nav-btn {
     padding: 10px 27px !important;
     border : 2px solid #fff;
     border-radius: 3px;
     margin-top: 5px;
   }
  .nav-btn:hover{
    color: #fff;
    background: $c-s;
    opacity: 0.9;
  }
}
.nav {
	.open {
		&>a {
			&,
			&:focus,
			&:hover {
				background-color: transparent;
			}
		}
	}
}
.navbar-nav>li>.dropdown-menu {
	left: 0;
	background-color: $gray-base;
	color: $c-s;
	.divider {
		background-color: #fff;
	}
	.dropdown-header {
		color: $gray-l-4;
	}
}
// section
section{
	position: relative;
	overflow: hidden;
	width: 100%;
	background: #fff;
}

.bnr-head {
	background: url('../images/bg-head.jpg') no-repeat;
	background-size: cover;
	min-height: 575px;
	text-align: center;
	h3 {
		margin-bottom: 30px;
		margin-top: 80px;
		font-size: 38px;
		color: #fff;
	}
	p {
		margin: 0 auto;
		max-width: 590px;
		font-size: 15px;
		color: #fff !important;
	}
	.btn {
		background: $c-t;
		color: #fff;
		margin: 0 10px;
		margin-top: 30px;
		&.btn-trans {
			background: rgba(255,255,255,0.5);
		}
	}
}
// dark layout
.dark-layout {
	header {
		@include linear-gradient-dark(rgba(0,0,0,5), rgba(0,0,0,0));
		.navbar li a {
			color: #fff;
		}
		h1, h2, h3, h4, h5, h6 {
			color: #fff !important;
		}
		p {
			color: #ababab;
		}
	}
}
